import React from 'react'
import { NavLink } from 'react-router-dom'
import styles from './style.module.css'
import NavIcon from '../Icons/icon'

const NavMobile = () => {
  const [nav, setNav] = React.useState(false)
  const [category, setCategory] = React.useState(false)

  const toggleNav = () => {
    setNav(!nav)
    setCategory(false)
  }

  return (
    <div className={styles.wrapper}>
      <nav>
        <ul>
          <li className={styles.submenu}><div onClick={toggleNav}><NavIcon /></div>
            {nav
              ? (
                <ul className={styles.nav}>
                  <li><NavLink to="/"></NavLink></li>
                  <li><NavLink to="/add"></NavLink></li>
                  <li><NavLink to="/profile"> </NavLink></li>
                  <li className={styles.submenu}><div onClick={() => setCategory(!category)}></div>
                    {category ? (
                      <ul className={styles.category}>
                        <li><NavLink to="/electronics"></NavLink></li>
                        <li><NavLink to="/household">  </NavLink></li>
                        <li><NavLink to="/clothes"></NavLink></li>
                        <li><NavLink to="/cars"></NavLink></li>
                        <li><NavLink to="/property"></NavLink></li>
                      </ul>
                    )
                      : null}
                  </li>
                  <li></li>
                </ul>
              )
              : null}
          </li>
        </ul>
      </nav>
    </div>
  )
}

export default NavMobile
