import React from 'react'
import styles from './style.module.css'

const UserInfo = ({ firstname, lastname, username, phone }) => (
  <div>
    <div className={styles.info}>
      <div>: {firstname}</div>
      <div>: {lastname}</div>
      <div>: {username}</div>
      <div>: {phone}</div>
    </div>
  </div>
)

export default UserInfo
