import React from 'react'

export default function ErrroMessage({ error }) {
  if (error) {
    switch (error.type) {
      case 'required':
        return <p style={{ color: 'red' }}>  </p>
      case 'minLength':
        return <p style={{ color: 'red' }}>      2 </p>
      case 'maxLength':
        return <p style={{ color: 'red' }}> 10  </p>
      case 'pattern':
        return <p style={{ color: 'red' }}>  </p>
      case 'min':
        return <p style={{ color: 'red' }}>   18 !</p>
      case 'max':
        return <p style={{ color: 'red' }}>  100 000 </p>
      case 'validate':
        return <p style={{ color: 'red' }}>Username is already used</p>
      default:
        return null
    }
  }

  return null
}
