import React from 'react'
import { NavLink } from 'react-router-dom'
import styles from './nav.module.css'

const CategoryNav = () => {
  const categoryItem = [
    { id: 1, link: '/electronics', text: '' },
    { id: 2, link: '/household', text: '  ' },
    { id: 3, link: '/clothes', text: '' },
    { id: 4, link: '/cars', text: '' },
    { id: 5, link: '/property', text: '' },
  ]

  return (
    <div>
      <nav>
        <ul className={styles.category}>
          {
                        categoryItem.map((category) => (
                          <li key={category.id}>
                            <NavLink
                              to={category.link}
                              className={styles.items}
                              activeClassName={styles.active}
                              exact
                            >
                              {category.text}
                            </NavLink>
                          </li>
                        ))
                    }
        </ul>
      </nav>
    </div>
  )
}

export default CategoryNav
